import { VFC, useState } from 'react'
import { Drawer } from 'ui-components/Drawer'
import { Wizard } from 'ui-components/Wizard'
import { Form } from './Form'
import { CreateSuccess } from './CreateSuccess'
import { CreateSurveyDrawerProps } from './types'
import { FormProps } from './Form/types'

export const CreateSurveyDrawer: VFC<CreateSurveyDrawerProps> = ({
  onCreate,
  copy,
  edit,
  ...rest
}) => {
  const [survey, setSurveyName] = useState('')

  const handleCreateSuccess: FormProps['onCreateSuccess'] = name => {
    setSurveyName(name)
    onCreate?.()
  }

  return (
    <Drawer size="90%" {...rest}>
      <Wizard>
        <Form onCreateSuccess={handleCreateSuccess} copy={copy} edit={edit} />
        <CreateSuccess
          surveyName={survey}
          action={edit ? 'updated' : 'created'}
        />
      </Wizard>
    </Drawer>
  )
}
