import { useEffect, VFC } from 'react'
import { useFormState } from 'react-final-form'
import { FormState } from 'final-form'
import { defineMessages, useIntl } from 'react-intl'
import { showErrorToast, clearToast } from 'utils/toast'
import { FormValues } from './types'

const toastMessages = defineMessages({
  GenericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const FormError: VFC = () => {
  const { formatMessage } = useIntl()
  const {
    dirtySinceLastSubmit,
    submitError,
  }: FormState<FormValues> = useFormState({
    subscription: {
      dirtySinceLastSubmit: true,
      submitError: true,
    },
  })

  useEffect(() => {
    if (dirtySinceLastSubmit) {
      clearToast()
    } else {
      if (submitError) {
        showErrorToast(formatMessage(toastMessages.GenericError))
      }
    }
  }, [dirtySinceLastSubmit, submitError, formatMessage])

  return null
}
