import { VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { CheckboxSelect } from '@jifflenow/brightui/icon'
import { useDrawer } from 'ui-components/Drawer'
import { SuccessIcon } from 'ui-components/icons/SuccessIcon'
import {
  ActionButton,
  CheckIconContainer,
  Container,
  MessageContainer,
  Root,
  SuccessMessage,
} from './styled-components'
import { checkedIconOverride } from './overrides'
import { CreateSuccessProps } from './types'

const CheckIcon = () => (
  <CheckIconContainer>
    <CheckboxSelect size={10} overrides={checkedIconOverride} />
  </CheckIconContainer>
)

const ActionButtons = () => {
  const { closeDrawer } = useDrawer()

  return (
    <ActionButton type="button" onClick={() => closeDrawer()}>
      <FormattedMessage defaultMessage="Close" description="Button label" />
    </ActionButton>
  )
}

export const CreateSuccess: VFC<CreateSuccessProps> = ({
  surveyName: name,
  action,
}) => (
  <Root>
    <Container>
      <SuccessIcon size={208} />
      <MessageContainer>
        <CheckIcon />
        <SuccessMessage>
          {action === 'created' ? (
            <FormattedMessage
              defaultMessage="{name} Survey created successfully"
              description="Success message"
              values={{ name }}
            />
          ) : (
            <FormattedMessage
              defaultMessage="{name} Survey updated successfully"
              description="Success message"
              values={{ name }}
            />
          )}
        </SuccessMessage>
      </MessageContainer>
      <ActionButtons />
    </Container>
  </Root>
)
