import { FC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { ICellRendererParams } from '@ag-grid-community/core'
import { PLACEMENT } from '@jifflenow/brightui/popover'
import { StatefulMenu, OnItemSelect } from '@jifflenow/brightui/menu'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { ObservableQuery } from 'gql'
import {
  useBulkDeleteSurveysMutation,
  useBulkActiveSurveysMutation,
  useBulkInactiveSurveysMutation,
} from 'gql'
import {
  getSurveyMasters_getSurveyMasters_data,
  getSurveyMasters_getSurveyMasters_data_actions,
} from 'gql/types/getSurveyMasters'
import { InlineMenuOverrides as inlineMenuOverrides } from 'components/ag-grid'
import { ThreeDotsPopover } from 'ui-components/ThreeDotsPopover'
import { CreateSurveyDrawer } from '../CreateSurveyDrawer'
import { DeleteConfirmationDialog } from '../DeleteConfirmationDialog'
import { ActionItem } from './types'

const messages = defineMessages({
  viewEditDetails: {
    defaultMessage: 'View/Edit Details',
    description: 'Grid row action label',
  },
  makeActive: {
    defaultMessage: 'Make Active',
    description: 'Grid row action label',
  },
  makeInactive: {
    defaultMessage: 'Make Inactive',
    description: 'Grid row action label',
  },
  delete: {
    defaultMessage: 'Delete',
    description: 'Grid row action label',
  },
  duplicate: {
    defaultMessage: 'Duplicate',
    description: 'Grid row action label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  inactiveSuccess: {
    defaultMessage: 'Survey is disabled for Associated Engagement Types',
    description: 'Grid row action label',
  },
  activeSuccess: {
    defaultMessage:
      'Please enable survey for Associated Engagement types at Engagement types settings > Surveys',
    description: 'Grid row action label',
  },
  deleteSuccess: {
    defaultMessage: '{name} deleted successfully',
    description: 'Grid row action label',
  },
  duplicateMessage: {
    defaultMessage: '{name} Survey copied successfully.',
    description: 'Grid row action label',
  },
})

const aria = defineMessages({
  labelEngagementMore: {
    defaultMessage: 'Engagement more option',
    description: 'Actions on this engagement type',
  },
})

const onItemSelect: OnItemSelect = args => args.item.onItemSelect?.(args)

const Content: FC<
  ICellRendererParams & {
    value: getSurveyMasters_getSurveyMasters_data_actions
    data: getSurveyMasters_getSurveyMasters_data
    refetch?: ObservableQuery['refetch']
    onRowDelete?: () => void
  }
> = ({ value, data, node, api, refetch, onRowDelete }) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const [isEdit, setIsEdit] = useState<boolean>()
  const [showConfirmation, setShowConfirmation] = useState(false)

  const { mutateBulkActive } = useBulkActiveSurveysMutation({
    onError: () => {
      api.hideOverlay()
      showErrorToast(formatMessage(messages.genericError))
    },
    onCompleted: () => {
      refetch?.()
      api.hideOverlay()
      showSuccessToast(formatMessage(messages.activeSuccess))
    },
  })

  const { mutateBulkInactive } = useBulkInactiveSurveysMutation({
    onError: () => {
      api.hideOverlay()
      showErrorToast(formatMessage(messages.genericError))
    },
    onCompleted: () => {
      refetch?.()
      api.hideOverlay()
      showSuccessToast(formatMessage(messages.inactiveSuccess))
    },
  })

  const { mutateBulkDelete } = useBulkDeleteSurveysMutation({
    onError: () => {
      api.hideOverlay()
      showErrorToast(formatMessage(messages.genericError))
    },
    onCompleted: () => {
      api.hideOverlay()
      onRowDelete?.()
      api.applyTransaction({ remove: [node.data] })
      showSuccessToast(
        formatMessage(messages.deleteSuccess, { name: data.label })
      )
    },
  })

  const makeStatusActive = () => {
    api.showLoadingOverlay()
    mutateBulkActive({ variables: { uuids: [data.uuid] } })
  }

  const makeStatusInactive = () => {
    api.showLoadingOverlay()
    mutateBulkInactive({ variables: { uuids: [data.uuid] } })
  }

  const deleteSurvey = () => {
    setShowConfirmation(false)
    api.showLoadingOverlay()
    mutateBulkDelete({ variables: { uuids: [data.uuid] } })
  }

  const menu: ActionItem[] = [
    {
      label: formatMessage(messages.viewEditDetails),
      onItemSelect: () => {
        setIsOpen(true)
        setIsEdit(true)
      },
    },
    value.canActivate && {
      label: formatMessage(messages.makeActive),
      onItemSelect: makeStatusActive,
    },
    value.canDeactivate && {
      label: formatMessage(messages.makeInactive),
      onItemSelect: makeStatusInactive,
    },
    value.canDelete && {
      label: formatMessage(messages.delete),
      onItemSelect: () => setShowConfirmation(true),
    },
    value.canDuplicate && {
      label: formatMessage(messages.duplicate),
      onItemSelect: () => {
        setIsOpen(true)
        setIsEdit(false)
      },
    },
  ].filter(Boolean) as ActionItem[]

  return (
    <>
      <StatefulMenu
        items={menu}
        onItemSelect={onItemSelect}
        overrides={inlineMenuOverrides}
      />
      {isOpen && (
        <CreateSurveyDrawer
          isOpen
          onClose={() => setIsOpen(false)}
          onCreate={() => refetch?.()}
          {...(isEdit ? { edit: data } : { copy: data })}
        />
      )}
      {showConfirmation && (
        <DeleteConfirmationDialog
          onClose={() => setShowConfirmation(false)}
          onConfirm={deleteSurvey}
        />
      )}
    </>
  )
}

export const ActionCell: FC<ICellRendererParams> = props => {
  const { formatMessage } = useIntl()

  return (
    <ThreeDotsPopover
      popoverMargin={8}
      placement={PLACEMENT.bottomRight}
      content={<Content {...props} />}
      ariaLabel={formatMessage(aria.labelEngagementMore)}
    />
  )
}
