import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field, Validation } from 'ui-components/Field'
import { Input } from '@jifflenow/brightui/input'
import { FormControlStyled } from './styled-components'
import { SurveyNameFieldProps } from './types'

const messages = defineMessages({
  surveyLabel: {
    defaultMessage: 'Name of the survey',
    description: 'Field label',
  },
})

export const SurveyNameField: VFC<SurveyNameFieldProps> = ({ label }) => {
  const { formatMessage } = useIntl()

  return (
    <Field<string>
      name="name"
      type="text"
      validations={[Validation.REQUIRED]}
      render={({ input, meta }) => (
        <FormControlStyled
          label={label}
          error={(meta.dirty || meta.touched) && meta.error}
        >
          <Input
            {...input}
            id="name"
            autoComplete="off"
            placeholder={formatMessage(messages.surveyLabel)}
          />
        </FormControlStyled>
      )}
    />
  )
}
