import { styled } from '@jifflenow/brightui'
import { FormControl } from '@jifflenow/brightui/form-control'

export const FormControlStyled = styled(
  FormControl,
  ({ $theme: { sizing } }) => ({
    marginBottom: sizing.scale800,
  })
)

export const FieldLabel = styled<{ $bold?: boolean }, 'span'>(
  'span',
  ({ $theme: { colors, typography }, $bold }) => ({
    ...($bold ? typography.Subtitle1 : typography.Paragraph1),
    color: colors.mono900,
  })
)

export const FieldToggleWrapper = styled<{ $error: boolean }, 'div'>(
  'div',
  ({ $error, $theme: { sizing } }) => ({
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    paddingTop: $error ? sizing.scale100 : sizing.scale650,
    paddingBottom: sizing.scale650,
  })
)

export const GenericError = styled(
  'div',
  ({ $theme: { colors, sizing, typography } }) => ({
    ...typography.LabelSmall,
    color: colors.negative700,
    marginTop: sizing.scale100,
  })
)

export const ToggleContainer = styled('div', ({ $theme: { sizing } }) => ({
  marginRight: sizing.scale400,
  marginLeft: sizing.scale400,
}))
