import { VFC } from 'react'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import { FormattedMessage } from 'react-intl'
import {
  DrawerCloseButton,
  DrawerTitle,
  HeaderContainer,
} from 'ui-components/Drawer'
import {
  FieldToggleWrapper,
  GenericError,
  ToggleContainer,
  HeaderToggleContainer,
} from './styled-components'
import { HeaderProps } from './types'

export const Header: VFC<HeaderProps> = ({ onClose, copy, edit }) => (
  <HeaderContainer>
    <DrawerCloseButton onClick={onClose} />
    <DrawerTitle>
      {edit ? (
        <FormattedMessage
          defaultMessage="{label}"
          description="Drawer title"
          values={{ label: edit.label }}
        />
      ) : copy ? (
        <FormattedMessage
          defaultMessage="Copy a {label}"
          description="Drawer title"
          values={{ label: copy.label }}
        />
      ) : (
        <FormattedMessage
          defaultMessage="Create New Survey"
          description="Drawer title"
        />
      )}
    </DrawerTitle>
    <HeaderToggleContainer>
      <Field<boolean>
        name="status"
        render={({
          input: { value, onChange },
          meta: { error, submitError },
        }) => (
          <>
            {(submitError || error) && (
              <GenericError>{submitError || error}</GenericError>
            )}
            <FieldToggleWrapper $error={submitError || error}>
              <ToggleContainer>
                <Toggle
                  name="status"
                  checked={value}
                  onChange={(e: any) => onChange(e.target.checked)}
                />
              </ToggleContainer>
            </FieldToggleWrapper>
          </>
        )}
      />
    </HeaderToggleContainer>
  </HeaderContainer>
)
