import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { TextFilter, Option } from '@jifflenow/brightui/filter'
import {
  filterOverrides,
  FiltersContainer,
  FilterItem,
  DataOptions,
  FilterProps,
  Options,
} from 'components/ag-grid'
import { useSurveyPreferences } from '../SurveysPreferencesProvider'
import { UsersFilter } from './UsersFilter'
import { EngagementsTypeFilter } from './EngagementsTypeFilter'

const messages = defineMessages({
  status: {
    defaultMessage: 'Survey Status',
    description: 'Filter label',
  },
  createdBy: {
    defaultMessage: 'Created By',
    description: 'Filter label',
  },
  engagementType: {
    defaultMessage: 'Engagement type',
    description: 'Filter label',
  },
  surveyType: {
    defaultMessage: 'Survey type',
    description: 'Filter label',
  },
  active: {
    defaultMessage: 'Active',
    description: 'Filter option label',
  },
  inactive: {
    defaultMessage: 'Inactive',
    description: 'Filter option label',
  },
  pre: {
    defaultMessage: 'Pre',
    description: 'Filter option label',
  },
  post: {
    defaultMessage: 'Post',
    description: 'Filter option label',
  },
})

const idMapper = (option: Option) => option.id
const statusOptions: string[] = ['active', 'inactive']

export const Filters: FC<FilterProps> = ({ onChange }) => {
  const { formatMessage } = useIntl()

  const { patchPreferences, preferences } = useSurveyPreferences()

  const { statuses = [], createdBy = [], engagementTypes = [] } = preferences
  const optionMapper = (o: DataOptions) => ({ id: o.uuid, label: o.name })
  const strOptionMapper = (text: string) => ({
    id: text,
    label: formatMessage(messages[text as keyof typeof messages]),
  })

  return (
    <FiltersContainer>
      {/* <FilterItem>
        <TextFilter
          value={surveyTypes.map(strOptionMapper)}
          overrides={filterOverrides}
          label={formatMessage(messages.surveyType)}
          onChange={(surveyTypes: Option) => {
            onChange()
            patchPreferences({ surveyTypes: surveyTypes.map(idMapper) })
          }}
          options={surveyTypeOptions.map(strOptionMapper)}
        />
      </FilterItem> */}
      <FilterItem>
        <TextFilter
          value={statuses.map(strOptionMapper)}
          overrides={filterOverrides}
          label={formatMessage(messages.status)}
          onChange={(statuses: Option) => {
            onChange()
            patchPreferences({ statuses: statuses.map(idMapper) })
          }}
          options={statusOptions.map(strOptionMapper)}
        />
      </FilterItem>
      <FilterItem>
        <EngagementsTypeFilter
          label={formatMessage(messages.engagementType)}
          value={
            Array.isArray(engagementTypes)
              ? engagementTypes.map(optionMapper)
              : []
          }
          onChange={(engagementTypes: Option) => {
            onChange()
            patchPreferences({
              engagementTypes: engagementTypes.map(
                ({ id, label }: Options) => ({
                  uuid: id,
                  name: label,
                })
              ),
            })
          }}
        />
      </FilterItem>
      <FilterItem>
        <UsersFilter
          label={formatMessage(messages.createdBy)}
          value={createdBy.map(optionMapper)}
          onChange={(users: Option) => {
            onChange()
            patchPreferences({
              createdBy: users.map(({ id, label }: Options) => ({
                uuid: id,
                name: label,
              })),
            })
          }}
        />
      </FilterItem>
    </FiltersContainer>
  )
}
