import { useMemo, VFC } from 'react'
import { useFetchEngagementTypeSettings } from 'gql'
import { defineMessages, useIntl } from 'react-intl'
import { Option, Select, Value } from '@jifflenow/brightui/select'
import { Field } from 'ui-components/Field'
import { SurveyEntityMasterName } from 'gql/types/globalTypes'
import { FieldRenderProps } from 'react-final-form'
import { FormControlStyled } from './styled-components'

const messages = defineMessages({
  engagementTypesForSurveyLabel: {
    defaultMessage: 'Enable this survey for engagement types',
    description: 'Field label',
  },
  engagementTypeForSurveySearchPlaceholder: {
    defaultMessage: 'Select meeting by name',
    description: 'Field label',
  },
  engagementTypesForSurveyCaption: {
    defaultMessage:
      'Go to respective engagement settings to edit survey settings',
    description: 'Field caption',
  },
})

const EngagementTypesField: VFC<FieldRenderProps<Option[] | undefined>> = ({
  input,
  meta: { touched, error },
  entityMasterName,
}) => {
  const { formatMessage } = useIntl()

  const { fetchEngagementTypes } = useFetchEngagementTypeSettings({
    skip: !entityMasterName,
    variables: {
      ...(entityMasterName === SurveyEntityMasterName.Activity && {
        showOnlyGeneralEngagementTypes: true,
      }),
      ...(entityMasterName === SurveyEntityMasterName.Session && {
        showOnlySessionEngagementTypes: true,
      }),
    },
  })

  const options = useMemo(
    () =>
      (
        fetchEngagementTypes?.map(({ uuid, name, displayOrder }) => ({
          id: uuid,
          label: name,
          displayOrder,
        })) ?? []
      ).sort((a, b) => a.displayOrder - b.displayOrder),
    [fetchEngagementTypes]
  )

  return (
    <FormControlStyled
      label={formatMessage(messages.engagementTypesForSurveyLabel)}
      caption={formatMessage(messages.engagementTypesForSurveyCaption)}
      error={touched && error}
    >
      <Select
        {...input}
        id="engagementTypeForSurvey"
        multi
        options={options}
        backspaceClearsInputValue
        searchable
        placeholder={formatMessage(
          messages.engagementTypeForSurveySearchPlaceholder
        )}
        onChange={({ value }: { value: Value[] }) => input.onChange(value)}
        disabled={!entityMasterName}
      />
    </FormControlStyled>
  )
}

export const EngagementTypesForSurveyField: VFC<{
  entityMasterName?: string
}> = ({ entityMasterName }) => (
  <Field<Option[] | undefined>
    name="engagementTypeForSurvey"
    format={(value: Option[] | undefined) => value ?? []}
    parse={(value: Option[]) => (value?.length ? value : undefined)}
    render={EngagementTypesField}
    entityMasterName={entityMasterName}
  />
)
