import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import {
  FieldLabel,
  FieldToggleWrapper,
  GenericError,
  ToggleContainer,
} from './styled-components'

const messages = defineMessages({
  companyLogoLabel: {
    defaultMessage: 'Do you want to display company logo on the survey page?',
    description: 'Field label',
  },
})

export const DisplayCompanyLogoField: VFC = () => {
  const { formatMessage } = useIntl()

  return (
    <Field<boolean>
      name="displayLogo"
      render={({
        input: { value, onChange },
        meta: { error, submitError },
      }) => (
        <>
          {(submitError || error) && (
            <GenericError>{submitError || error}</GenericError>
          )}
          <FieldToggleWrapper $error={submitError || error}>
            <FieldLabel>{formatMessage(messages.companyLogoLabel)}</FieldLabel>
            <ToggleContainer>
              <Toggle
                name="displayLogo"
                ariaLabel={formatMessage(messages.companyLogoLabel)}
                checked={value}
                onChange={(e: any) => onChange(e.target.checked)}
              />
            </ToggleContainer>
          </FieldToggleWrapper>
        </>
      )}
    />
  )
}
