import { VFC, useEffect, useState, useMemo } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldRenderProps } from 'react-final-form'
import { Field, Validation } from 'ui-components/Field'
import { Option, Select, Value } from '@jifflenow/brightui/select'
import {
  SurveyEntityMasterName,
  SurveyExecutionType,
} from 'gql/types/globalTypes'
import { FormControlStyled } from './styled-components'
import { SurveyExecutionTypeFieldProps } from './types'

const messages = defineMessages({
  surveyExecutionTypeLabel: {
    defaultMessage: 'When to collect survey responses',
    description: 'Field label',
  },
  surveyExecutionTypeSearchPlaceholder: {
    defaultMessage: 'Search by name',
    description: 'Field label',
  },
  postMeeting: {
    defaultMessage: 'Post-Meeting',
    description: 'Form option label',
  },
  postSession: {
    defaultMessage: 'Post-Session',
    description: 'Form option label',
  },
  preSession: {
    defaultMessage: 'Pre-Session',
    description: 'Form option label',
  },
})

const ExecutionTypeField: VFC<FieldRenderProps<string>> = ({
  input,
  meta: { touched, error },
  label,
  entityMasterName,
  disabled,
}) => {
  const { formatMessage } = useIntl()
  const [value, setValue] = useState<Option>([])

  const options = useMemo(
    () =>
      entityMasterName === SurveyEntityMasterName.Activity
        ? [
            {
              id: SurveyExecutionType.post,
              label: formatMessage(messages.postMeeting),
            },
          ]
        : [
            {
              id: SurveyExecutionType.pre,
              label: formatMessage(messages.preSession),
            },
            {
              id: SurveyExecutionType.post,
              label: formatMessage(messages.postSession),
            },
          ],
    [formatMessage, entityMasterName]
  )

  useEffect(() => {
    if (disabled) {
      setValue(options.filter(option => input.value === option.id))
    } else {
      if (entityMasterName === SurveyEntityMasterName.Activity) {
        setValue(options)
        input.onChange(options[0].id)
      } else {
        setValue([])
        input.onChange(undefined)
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [options, entityMasterName])

  const handleOnChange = (value: Value) => {
    setValue(value)
    input.onChange(value[0].id)
  }

  return (
    <FormControlStyled label={label} error={touched && error}>
      <Select
        {...input}
        id="executionType"
        clearable={false}
        value={value}
        options={options}
        backspaceClearsInputValue
        searchable
        onChange={({ value }: { value: Value }) => handleOnChange(value)}
        disabled={disabled || !entityMasterName}
      />
    </FormControlStyled>
  )
}

export const SurveyExecutionTypeField: VFC<SurveyExecutionTypeFieldProps> = ({
  entityMasterName,
  isDisabled,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Field<string>
      name="executionType"
      label={formatMessage(messages.surveyExecutionTypeLabel)}
      render={ExecutionTypeField}
      entityMasterName={entityMasterName}
      validations={[Validation.REQUIRED]}
      disabled={isDisabled}
    />
  )
}
