import { FC } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBulkActiveSurveysMutation } from 'gql'
import { bulkActivateSurveyMasters as Data } from 'gql/types/bulkActivateSurveyMasters'
import { BulkButtonProps } from './types'
import { Button } from './styled-components'
import { Message } from './Messages'

const messages = defineMessages({
  allActive: {
    defaultMessage: 'Make all active',
    description: 'Button text',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

const showToast = ({ bulkActivateSurveyMasters }: Data) => {
  const { failureCount, successCount } = bulkActivateSurveyMasters
  failureCount === 0
    ? showSuccessToast(
        <Message
          message={
            <FormattedMessage
              defaultMessage="{successCount, number} survey {successCount, plural, one {type} other {types}} activated successfully"
              description="Toast message"
              values={{ successCount }}
            />
          }
        />
      )
    : showErrorToast(
        <Message
          message={
            <FormattedMessage
              defaultMessage="Survey activation failed"
              description="Toast message"
            />
          }
        />
      )
}

export const MakeActiveButton: FC<BulkButtonProps> = props => {
  const { gridApi, variables, resetToFirstPage } = props
  const { formatMessage } = useIntl()
  const { mutateBulkActive } = useBulkActiveSurveysMutation({
    onCompleted: data => {
      resetToFirstPage()
      showToast(data)
    },
    onError: () => {
      gridApi.hideOverlay()
      showErrorToast(<Message message={formatMessage(messages.genericError)} />)
    },
  })

  return (
    <Button
      kind={KIND.secondary}
      onClick={() => {
        gridApi.showLoadingOverlay()
        mutateBulkActive({ variables })
      }}
    >
      {formatMessage(messages.allActive)}
    </Button>
  )
}
