import { FC, useState } from 'react'
import { List as AgGridList } from 'components/ag-grid'
import { RowClickedEvent } from '@ag-grid-community/core'
import { getSurveyMasters_getSurveyMasters_data } from 'gql/types/getSurveyMasters'
import { StatusCell } from 'components/EngagementTypes'
import { CreateSurveyDrawer } from '../CreateSurveyDrawer'
import { NoRowsAndError } from './NoRowsAndError'
import { ActionCell } from './ActionCell'
import { ListProps } from './types'
import * as constants from './constants'

export const List: FC<
  ListProps<getSurveyMasters_getSurveyMasters_data>
> = props => {
  const [isOpen, setIsOpen] = useState(false)
  const [data, setData] = useState(undefined)

  const onRowClicked = ({ data, event }: RowClickedEvent) => {
    const rowElement = event?.target as Element
    if (!rowElement.closest('[col-id=actions]')) {
      setIsOpen(true)
      setData(data)
    }
  }

  return (
    <>
      <AgGridList
        {...props}
        constants={constants}
        frameworkComponents={{
          actionCell: ActionCell,
          statusCell: StatusCell,
          noRowsAndError: NoRowsAndError,
        }}
        onRowClicked={onRowClicked}
      />
      {isOpen && (
        <CreateSurveyDrawer
          isOpen
          onClose={() => setIsOpen(false)}
          onCreate={() => props.refetch?.()}
          edit={data}
        />
      )}
    </>
  )
}
