import { VFC, useState, useEffect, useMemo } from 'react'
import { Prompt } from 'react-router-dom'
import { Form as FinalForm } from 'react-final-form'
import createFocusDecorator from 'final-form-focus'
import createDecorator from 'final-form-calculate'
import { Config, FORM_ERROR } from 'final-form'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Option } from '@jifflenow/brightui/select'
import { useDrawer } from 'ui-components/Drawer'
import { useWizard } from 'ui-components/Wizard'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { mutateCreateSurvey, mutateDuplicateSurvey, useApolloClient } from 'gql'
import { upsertSurveyMasterVariables } from 'gql/types/upsertSurveyMaster'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import {
  SurveyNameField,
  EntityMasterNameField,
  SurveyExecutionTypeField,
  EngagementTypesForSurveyField,
  DisplayCompanyLogoField,
} from './Fields'
import { Header as PageHeader } from './Header'
import { Footer } from './Footer'
import { FormValues, FormProps, SurveyFormProps } from './types'
import { FormError } from './FormError'
import { Body, FieldWrapper } from './styled-components'

const messages = defineMessages({
  promptMessage: {
    defaultMessage: 'Are you sure you want to leave?',
    description: 'Prompt message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  surveyNameLabel: {
    defaultMessage: 'Name of the Survey',
    description: 'Field label',
  },
  surveyName: {
    defaultMessage: 'Copy {name}',
    description: 'Help text',
  },
})

const modalLabels = defineMessages({
  modalHeader: {
    defaultMessage: 'Your changes will be lost.',
    description: 'Modal heading',
  },
  modalMessage: {
    defaultMessage: 'Would you like to stay?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Leave',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Stay',
    description: 'Button label',
  },
})

const SurveyForm: VFC<SurveyFormProps> = ({ handleSubmit, dirty, ...rest }) => {
  const { formatMessage } = useIntl()
  const { closeDrawer } = useDrawer()
  const unload = useBeforeUnload()

  const [showConfirmation, setShowConfirmation] = useState(false)

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  const handleDrawerClose = () => {
    if (dirty) {
      setShowConfirmation(true)
      return
    }
    closeDrawer()
  }

  const handleDialogClose = () => setShowConfirmation(false)

  const handleDialogCancel = () => {
    setShowConfirmation(false)
    closeDrawer()
  }

  return (
    <form onSubmit={handleSubmit} noValidate>
      <Prompt when={dirty} message={formatMessage(messages.promptMessage)} />
      {showConfirmation && (
        <ConfirmationDialog
          heading={formatMessage(modalLabels.modalHeader)}
          message={formatMessage(modalLabels.modalMessage)}
          cancelLabel={formatMessage(modalLabels.cancelButtonLabel)}
          successLabel={formatMessage(modalLabels.successButtonLabel)}
          onClose={handleDialogClose}
          onCancel={handleDialogCancel}
          onSuccess={handleDialogClose}
        />
      )}
      <PageHeader onClose={handleDrawerClose} {...rest} />
      <Body>
        <FieldWrapper>
          <SurveyNameField label={formatMessage(messages.surveyNameLabel)} />
          <EntityMasterNameField
            isDisabled={rest.edit || rest.copy ? true : false}
          />
          <SurveyExecutionTypeField
            isDisabled={rest.edit || rest.copy ? true : false}
            entityMasterName={rest.values?.entityMasterName}
          />
          <EngagementTypesForSurveyField
            entityMasterName={rest.values?.entityMasterName}
          />
          <DisplayCompanyLogoField />
        </FieldWrapper>
        <FormError />
      </Body>
      <Footer onCancelClick={handleDrawerClose} {...rest} />
    </form>
  )
}

const focusDecorator = createFocusDecorator<FormValues>()

const roleFieldDecorator = createDecorator({
  field: 'entityMasterName',
  updates: {
    engagementTypeForSurvey: (_, allValues, prevValues) => {
      const { entityMasterName } = prevValues as FormValues
      if (entityMasterName) {
        return undefined
      }
      const { engagementTypeForSurvey } = allValues as FormValues

      return engagementTypeForSurvey
    },
  },
})

const EngagementUuidMapper = ({ id }: Option) => id as string

export const Form: VFC<FormProps> = ({ onCreateSuccess, copy, edit }) => {
  const { formatMessage } = useIntl()
  const { nextStep } = useWizard()
  const client = useApolloClient()

  const [showLoader, setShowLoader] = useState(false)
  const [editState, setEditState] = useState(false)

  const handleSubmit: Config<FormValues>['onSubmit'] = async values => {
    try {
      const {
        name,
        engagementTypeForSurvey,
        entityMasterName,
        displayLogo,
        status,
        uuid,
        executionType,
      } = values

      setShowLoader(true)
      const commonParams = {
        label: name,
        status,
        displayLogo,
        ...(engagementTypeForSurvey && {
          engagementUuids: engagementTypeForSurvey.map(EngagementUuidMapper),
        }),
      }

      if (copy) {
        await mutateDuplicateSurvey(client, {
          variables: { copySurveyMasterId: copy.id, ...commonParams },
        })
      } else {
        const variables: upsertSurveyMasterVariables = {
          ...commonParams,
          entityMasterName,
          executionType,
          uuid: editState ? uuid : null,
        }
        await mutateCreateSurvey(client, { variables })
      }

      setShowLoader(false)
      onCreateSuccess(name)
      nextStep()
      return undefined
    } catch (error) {
      setShowLoader(false)
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  useEffect(() => edit && setEditState(true), [edit])

  const initialValues: FormValues | {} = useMemo(() => {
    if (copy || edit) {
      const {
        label,
        associatedEngagementTypeDetails,
        entityMasterName,
        executionType,
        displayLogo,
        status,
        uuid,
      } = copy ?? edit!

      return {
        name: copy
          ? formatMessage(messages.surveyName, {
              name: label,
            })
          : label,
        status,
        displayLogo,
        entityMasterName,
        executionType,
        ...(associatedEngagementTypeDetails?.length
          ? {
              engagementTypeForSurvey: associatedEngagementTypeDetails?.map(
                ({ uuid, name }) => ({
                  id: uuid,
                  label: name,
                })
              ),
            }
          : null),
        uuid,
      }
    } else {
      return {
        status: true,
        displayLogo: false,
      }
    }
  }, [copy, edit, formatMessage])

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        decorators={[
          focusDecorator,
          // TODO: Awaiting fix for this [bug](https://github.com/final-form/final-form-calculate/issues/38)
          // to remove `ts-expect-error`.
          // @ts-expect-error
          roleFieldDecorator,
        ]}
        onSubmit={handleSubmit}
        initialValues={initialValues}
        render={props => <SurveyForm {...props} copy={copy} edit={edit} />}
      />
    </>
  )
}
