import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldRenderProps } from 'react-final-form'
import { Option, Select } from '@jifflenow/brightui/select'
import { useEventLocation } from 'providers/LocationProvider'
import { Field, Validation } from 'ui-components/Field'
import { SurveyEntityMasterName } from 'gql/types/globalTypes'
import { FormControlStyled } from './styled-components'
import { EntityMasterNameFieldProps } from './types'

const messages = defineMessages({
  surveyLabel: {
    defaultMessage: 'Survey for',
    description: 'Field label',
  },
  surveySearchPlaceholder: {
    defaultMessage: 'Search by name',
    description: 'Field label',
  },
  meeting: {
    defaultMessage: 'Meeting',
    description: 'Form Option label',
  },
  briefing: {
    defaultMessage: 'Briefing',
    description: 'Form Option label',
  },
  session: {
    defaultMessage: 'Session',
    description: 'Form Option label',
  },
})

const MasterName: VFC<FieldRenderProps<string>> = ({
  input,
  meta: { touched, error },
  disabled,
}) => {
  const { formatMessage } = useIntl()

  const { track_and_session } = useEventLocation().configs || {}

  const options = [
    {
      id: SurveyEntityMasterName.Activity,
      label: formatMessage(messages.meeting),
    },
    track_and_session && {
      id: SurveyEntityMasterName.Session,
      label: formatMessage(messages.session),
    },
  ].filter(Boolean) as Option[]

  const value = options.filter(({ id }) => id === input.value)

  return (
    <FormControlStyled
      label={formatMessage(messages.surveyLabel)}
      error={touched && error}
    >
      <Select
        {...input}
        clearable={false}
        id="entityMasterName"
        value={value}
        options={options}
        backspaceClearsInputValue
        searchable
        placeholder={formatMessage(messages.surveyLabel)}
        onChange={({ value }: { value: Option[] }) =>
          input.onChange(value[0].id)
        }
        required
        disabled={disabled}
      />
    </FormControlStyled>
  )
}

export const EntityMasterNameField: VFC<EntityMasterNameFieldProps> = ({
  isDisabled,
}) => (
  <Field<string>
    name="entityMasterName"
    render={MasterName}
    validations={[Validation.REQUIRED]}
    disabled={isDisabled}
  />
)
