import { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { TextFilter } from '@jifflenow/brightui/filter'
// TODO: check if api will be different for
import { useSurveyCreatedByUsers } from 'rest'
import { useDebounce } from 'utils/debounce'
import { Options, UsersFilterProps } from 'components/ag-grid'

export const UsersFilter = ({ value, label, onChange }: UsersFilterProps) => {
  const { eventName } = useParams<{
    eventName: string
  }>()
  const location = new URLSearchParams(window.location.search).get('location')
  const [searchText, setSearchText] = useState('')
  const [options, setOptions] = useState<Options[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const search = useDebounce(searchText)

  const { run: fetchUsers } = useSurveyCreatedByUsers({
    subSystem: eventName,
    onResolve: ({ data }) => {
      setOptions(
        data.map(({ id, attributes: { firstName, lastName } }) => ({
          id,
          label: `${firstName} ${lastName}`,
        }))
      )
      setIsLoading(false)
    },
  })

  useEffect(() => {
    !search ? setOptions([]) : fetchUsers({ search, locationId: location })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search])

  return (
    <TextFilter
      label={label}
      onChange={onChange}
      options={options}
      value={value}
      asyncOptions
      searchText={searchText}
      onSearch={setSearchText}
      isLoading={isLoading}
    />
  )
}
