import { useMemo } from 'react'
import { useFetchEngagementTypeSettings } from 'gql'
import { TextFilter } from '@jifflenow/brightui/filter'
import { EngagementsTypeFilterProps } from './types'

export const EngagementsTypeFilter = ({
  value,
  label,
  onChange,
}: EngagementsTypeFilterProps) => {
  const { fetchEngagementTypes, loading } = useFetchEngagementTypeSettings({
    variables: {
      showOnlyNonConsecutiveEngagementTypes: true,
    },
  })

  const options = useMemo(
    () =>
      (
        fetchEngagementTypes?.map(({ uuid, name, displayOrder }) => ({
          id: uuid,
          label: name,
          displayOrder,
        })) ?? []
      ).sort((a, b) => a.displayOrder - b.displayOrder),
    [fetchEngagementTypes]
  )

  return (
    <TextFilter
      label={label}
      onChange={onChange}
      options={options}
      value={value}
      searchable
      isLoading={loading}
    />
  )
}
