import { FC } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBulkInactiveSurveysMutation } from 'gql'
import { bulkInactivateSurveyMasters as Data } from 'gql/types/bulkInactivateSurveyMasters'
import { Message } from './Messages'
import { BulkButtonProps } from './types'
import { Button } from './styled-components'

const messages = defineMessages({
  allInactive: {
    defaultMessage: 'Make all inactive',
    description: 'Button text',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

const showToast = ({ bulkInactivateSurveyMasters: data }: Data) => {
  const { totalCount, failureCount, successCount, surveyMasterDetails } = data
  failureCount === 0
    ? showSuccessToast(
        <Message
          message={
            <FormattedMessage
              defaultMessage="{successCount, number} survey {successCount, plural, one {type} other {types}} inactivated successfully"
              description="Toast message"
              values={{ successCount }}
            />
          }
        />
      )
    : failureCount === totalCount
    ? showErrorToast(
        <Message
          details={surveyMasterDetails}
          message={
            <FormattedMessage
              defaultMessage="Survey inactivation failed"
              description="Toast message"
            />
          }
        />
      )
    : showErrorToast(
        <Message
          details={surveyMasterDetails}
          message={
            <FormattedMessage
              defaultMessage="Failed to inactivate {failureCount, number} out of {totalCount, number} survey {totalCount, plural, one {type} other {types}}"
              description="Toast message"
              values={{ failureCount, totalCount }}
            />
          }
        />,
        { autoHideDuration: 0 }
      )
}

export const MakeInactiveButton: FC<BulkButtonProps> = props => {
  const { gridApi, variables, resetToFirstPage } = props
  const { formatMessage } = useIntl()
  const { mutateBulkInactive } = useBulkInactiveSurveysMutation({
    onCompleted: data => {
      resetToFirstPage()
      showToast(data)
    },
    onError: () => {
      gridApi.hideOverlay()
      showErrorToast(<Message message={formatMessage(messages.genericError)} />)
    },
  })

  return (
    <Button
      kind={KIND.secondary}
      onClick={() => {
        gridApi.showLoadingOverlay()
        mutateBulkInactive({ variables })
      }}
    >
      {formatMessage(messages.allInactive)}
    </Button>
  )
}
