import { VFC } from 'react'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { defineMessages, useIntl } from 'react-intl'

const messages = defineMessages({
  modalHeader: {
    defaultMessage: 'Are you sure you want to delete the Survey?',
    description: 'Modal header',
  },
  modalMessage: {
    defaultMessage: 'Please note: This action cannot be reverted.',
    description: 'Confirmation messages',
  },
  cancelButtonLabel: {
    defaultMessage: 'No',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Yes',
    description: 'Button label',
  },
})

export const DeleteConfirmationDialog: VFC<{
  onClose: () => void
  onConfirm: () => void
}> = ({ onClose, onConfirm }) => {
  const { formatMessage } = useIntl()

  return (
    <ConfirmationDialog
      heading={formatMessage(messages.modalHeader)}
      message={formatMessage(messages.modalMessage)}
      cancelLabel={formatMessage(messages.cancelButtonLabel)}
      successLabel={formatMessage(messages.successButtonLabel)}
      closeable
      onClose={onClose}
      onCancel={onClose}
      onSuccess={onConfirm}
    />
  )
}
