import {
  useContext,
  FC,
  useMemo,
  useState,
  useEffect,
  createContext,
} from 'react'
import { useParams } from 'react-router-dom'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import merge from 'deepmerge'
import {
  IfRejected,
  useSurveyPreferences as useGetSurveyPreferences,
  patchSurveyPreferences as patch,
} from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { components } from 'types/openapi'

type SurveyPreferences = components['schemas']['SurveyPreferences']
type preferencesT = SurveyPreferences['data']
type ContextT = {
  preferences: preferencesT
  patchPreferences: (preferences: preferencesT) => void
}

const INITIAL_STATE: {
  sortBy: preferencesT['sortBy']
  sortOrder: preferencesT['sortOrder']
} = {
  sortBy: 'label',
  sortOrder: 'ASC',
}
const Context = createContext<ContextT | null>(null)
const SURVEY_PREFERENCES = 'surveyPreferences'

const Provider: FC<ContextT> = ({
  children,
  preferences,
  patchPreferences,
}) => {
  const value = useMemo<ContextT>(() => ({ preferences, patchPreferences }), [
    preferences,
    patchPreferences,
  ])

  return <Context.Provider value={value}>{children}</Context.Provider>
}

const usePreferences = () => {
  const { eventName } = useParams<{ eventName: string }>()
  const location = new URLSearchParams(window.location.search).get('location')

  const [preferences, setPreferences] = useState<preferencesT>({})

  useEffect(() => {
    const handleLocalStorageChange = (e: StorageEvent) => {
      if (e.key === SURVEY_PREFERENCES) {
        setPreferences(JSON.parse(e.newValue || '{}'))
      }
    }
    window.addEventListener('storage', handleLocalStorageChange)
    return () => {
      window.removeEventListener('storage', handleLocalStorageChange)
    }
  }, [setPreferences])

  const initPreferences = (data: SurveyPreferences) => {
    const initialPreferences = { ...INITIAL_STATE, ...data.data }
    setPreferences(initialPreferences)
    window.localStorage.setItem(
      SURVEY_PREFERENCES,
      JSON.stringify(initialPreferences)
    )
  }

  const patchPreferences = (payload: preferencesT) => {
    const pref = merge(preferences, payload, { arrayMerge: (_, s) => s })
    setPreferences(pref)
    patch({
      payload: { data: payload },
      subSystem: eventName,
      locationId: location!,
    }).then(() =>
      window.localStorage.setItem(SURVEY_PREFERENCES, JSON.stringify(pref))
    )
  }

  return { preferences, initPreferences, patchPreferences }
}

const SurveyPreferencesFetcher: FC<{
  onResolve: (data: SurveyPreferences) => void
}> = props => {
  const { eventName } = useParams<{ eventName: string }>()
  const location = new URLSearchParams(window.location.search).get('location')

  const state = useGetSurveyPreferences({
    ...props,
    subSystem: eventName,
    locationId: location!,
  })

  return (
    <>
      {!state.isSettled && <ModalSpinner />}
      <IfRejected state={state}>
        {() => <ErrorPage onButtonClick={state.reload} />}
      </IfRejected>
    </>
  )
}

export const SurveysPreferencesProvider: FC = ({ children }) => {
  const { preferences, initPreferences, patchPreferences } = usePreferences()

  return Object.keys(preferences).length === 0 ? (
    <SurveyPreferencesFetcher onResolve={initPreferences} />
  ) : (
    <Provider preferences={preferences} patchPreferences={patchPreferences}>
      {children}
    </Provider>
  )
}

export const useSurveyPreferences = () => {
  const context = useContext(Context)!
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useSurveyPreferences must be used within a SurveyPreferencesProvider'
    )
  }

  return context
}
