import { VFC, useState, ChangeEvent, useEffect } from 'react'
import { GridApi } from '@ag-grid-community/core'
import { useEventSurveyListQuery } from 'gql'
import {
  AllowedSortFormat,
  AllowedStatusKeys,
  SurveyMasterAllowedSortKeys,
  SurveyExecutionType,
} from 'gql/types/globalTypes'
import {
  RootContainer,
  SubHeaderRoot,
  Footer,
} from 'components/EngagementTypes'
import { useSelection, PAGE_SIZE } from 'components/ag-grid'
import { EventMainHeader } from 'components/EventMainHeader'
import { Pagination, KIND } from '@jifflenow/brightui/pagination'
import { useDebounce } from 'utils/debounce'
import { usePageSizes } from 'utils/usePageSizes'
import { useSurveyPreferences } from './SurveysPreferencesProvider'
import { List } from './List'
import { Header } from './Header'
import { Filters } from './Filters'
import { BulkActions } from './BulkActions'

export const SurveysList: VFC = () => {
  const [gridApi, setGridApi] = useState<GridApi | null>(null)
  const [search, setSearch] = useState<string>('')
  const searchTerm = useDebounce(search.trim(), 500)
  const [pageNumber, setPageNumber] = useState<number>(1)
  const { deselectAll, isSelectAll, getSelectedItems } = useSelection()

  const resetToFirstPage = () => {
    setPageNumber(1)
    deselectAll()
  }

  const listPreferences = useSurveyPreferences()
  const {
    sortBy,
    sortOrder,
    statuses,
    createdBy,
    surveyTypes,
    engagementTypes,
    pageSize,
  } = listPreferences.preferences

  const [currentPageSize, setCurrentPageSize] = useState(pageSize ?? PAGE_SIZE)

  const { loading, execute, page, data, refetch } = useEventSurveyListQuery({
    notifyOnNetworkStatusChange: true,
    onCompleted: () => gridApi?.hideOverlay(),
    onError: () => gridApi?.showNoRowsOverlay(),
  })

  const { total = 0 } = page ?? {}
  const pageSizes = usePageSizes(total)

  const refetchAndResetToFirstPage = () => {
    pageNumber === 1 && refetch()
    resetToFirstPage()
  }

  const rowDeleteHandler = () => {
    if (data?.length !== 1 || pageNumber === 1) {
      refetch()
    } else {
      setPageNumber(prevPageNumber => prevPageNumber - 1)
    }
  }

  useEffect(() => {
    execute({
      variables: {
        searchTerm,
        page: { size: currentPageSize, number: pageNumber },
        sortOrder: sortOrder as AllowedSortFormat,
        sortBy: sortBy as SurveyMasterAllowedSortKeys,
        filters: {
          createdBy: createdBy?.map(({ uuid }) => uuid),
          status: statuses as AllowedStatusKeys[],
          engagementTypeUuid: engagementTypes?.map(({ uuid }) => uuid),
          executionType: surveyTypes as SurveyExecutionType[],
        },
      },
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    statuses,
    createdBy,
    sortBy,
    sortOrder,
    searchTerm,
    pageNumber,
    engagementTypes,
    surveyTypes,
    currentPageSize,
  ])

  if (loading) {
    gridApi?.showLoadingOverlay()
  }

  return (
    <RootContainer>
      <EventMainHeader />
      <Header
        search={search}
        itemCount={total}
        onSearch={(e: ChangeEvent<HTMLInputElement>) => {
          resetToFirstPage()
          setSearch(e.target.value)
        }}
        onCreate={() => refetch()}
      />
      <SubHeaderRoot>
        <Filters onChange={resetToFirstPage} />
        {isSelectAll() || getSelectedItems().length ? (
          <BulkActions
            gridApi={gridApi!}
            totalItems={total}
            appliedSearchTerm={searchTerm}
            resetToFirstPage={refetchAndResetToFirstPage}
          />
        ) : null}
      </SubHeaderRoot>
      <List
        gridApi={gridApi}
        setGridApi={setGridApi}
        onSort={resetToFirstPage}
        paginationPageSize={currentPageSize}
        onRowDelete={rowDeleteHandler}
        {...listPreferences}
        {...{ data, refetch }}
      />
      {total > PAGE_SIZE && (
        <Footer>
          <Pagination
            kind={KIND.light}
            totalItems={total}
            currentPageNumber={pageNumber}
            onPageNumberChange={setPageNumber}
            pageSizes={pageSizes}
            currentPageSize={currentPageSize}
            onPageSizeChange={pageSize => {
              setCurrentPageSize(pageSize)
              setPageNumber(1)
              listPreferences.patchPreferences({ pageSize })
            }}
          />
        </Footer>
      )}
    </RootContainer>
  )
}
