import { VFC } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import { Input } from '@jifflenow/brightui/input'
import { Search } from '@jifflenow/brightui/icon'
import {
  Container,
  RightContainer,
  InputControl,
  Root,
  Title,
  PageHint,
  HeaderProps,
} from 'components/EngagementTypes'
import { useEventLocation } from 'providers/LocationProvider'
import { NewSurveyButton } from '../NewSurveyButton'

const messages = defineMessages({
  search: {
    defaultMessage: 'Search',
    description: 'Placeholder text',
  },
})

export const Header: VFC<HeaderProps> = ({
  itemCount,
  onSearch,
  search,
  onCreate,
}) => {
  const { formatMessage } = useIntl()

  const version = useEventLocation().location?.attributes.version ?? ''

  return (
    <Root>
      <Container>
        <Title>
          <FormattedMessage
            defaultMessage="Surveys  {count, number}"
            description="Section title"
            values={{ count: itemCount }}
          />
        </Title>
        <PageHint>
          <FormattedMessage
            defaultMessage="Manage survey creation, modification and activation."
            description="Section sub title"
          />
        </PageHint>
      </Container>
      <RightContainer>
        <InputControl>
          <Input
            type="search"
            value={search}
            IconAfter={Search}
            onChange={onSearch}
            placeholder={formatMessage(messages.search)}
          />
        </InputControl>
        {version !== '1' && <NewSurveyButton onCreate={onCreate} />}
      </RightContainer>
    </Root>
  )
}
