import { FC } from 'react'
import { FormattedMessage } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import {
  NoRowsIcon,
  ErrorIcon,
  NoRowsContainer,
  NoRowsText,
  ButtonRetry,
} from 'components/ag-grid'
import { useEventLocation } from 'providers/LocationProvider'
import { NewSurveyButton } from '../NewSurveyButton'
import { NoRowsAndErrorOverlayProps } from './types'

export const NoRowsAndError: FC<NoRowsAndErrorOverlayProps> = ({
  agGridReact,
  refetch,
}) => {
  const showError = agGridReact.props.rowData === undefined

  const version = useEventLocation().location?.attributes.version ?? ''

  return (
    <NoRowsContainer>
      {showError ? <ErrorIcon size={93} /> : <NoRowsIcon size={93} />}
      <NoRowsText>
        {showError ? (
          <FormattedMessage
            defaultMessage="Something went wrong. Please try again."
            description="Error message"
          />
        ) : version === '1' ? (
          <FormattedMessage
            defaultMessage="This instance has been created using the legacy system. Please use the old/legacy UI to configure the same."
            description="To show message if event is v1"
          />
        ) : (
          <FormattedMessage
            defaultMessage="No Survey to show"
            description="Empty state of the list"
          />
        )}
      </NoRowsText>
      {showError ? (
        <ButtonRetry type="button" kind={KIND.secondary} onClick={refetch}>
          <FormattedMessage defaultMessage="Retry" description="Button label" />
        </ButtonRetry>
      ) : (
        version !== '1' && <NewSurveyButton onCreate={refetch} />
      )}
    </NoRowsContainer>
  )
}
