import { FC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { Modal, ModalHeader, SIZE } from '@jifflenow/brightui/modal'
import { modalOverrides } from 'components/EngagementTypes'
import { MessageProps } from './types'
import {
  Button,
  MessageRoot,
  MessageRow,
  StyledCheckCircleOutlined,
  StyledCloseCircleOutlined,
  Name,
  ModalBody,
} from './styled-components'

const messages = defineMessages({
  view: {
    defaultMessage: 'View',
    description: 'Button text',
  },
})
// TODO: Refactor: try to trigger toasts here by receiving other details as props
export const Message: FC<MessageProps> = ({ message, details }) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const messageList = details?.sort((first, second) =>
    first.success === second.success ? 0 : first.success ? -1 : 1
  )

  return (
    <MessageRoot>
      {message}
      {!!details?.length && (
        <>
          <Button kind={KIND.secondary} onClick={() => setIsOpen(true)}>
            {formatMessage(messages.view)}
          </Button>
          {isOpen && (
            <Modal
              isOpen
              size={SIZE.medium}
              overrides={modalOverrides}
              onClose={() => setIsOpen(false)}
            >
              <ModalHeader>{message}</ModalHeader>
              <ModalBody>
                {messageList?.map(({ success, name }, index) => (
                  <MessageRow key={index}>
                    {success ? (
                      <StyledCheckCircleOutlined size="20px" />
                    ) : (
                      <StyledCloseCircleOutlined size="20px" />
                    )}
                    <Name>{name}</Name>
                  </MessageRow>
                ))}
              </ModalBody>
            </Modal>
          )}
        </>
      )}
    </MessageRoot>
  )
}
