import { VFC } from 'react'
import { StatefulPopover } from '@jifflenow/brightui/popover'
import { IconButton } from '@jifflenow/brightui/button'
import { ThreeDots } from '@jifflenow/brightui/icon'
import { popoverOverrides, threeDotButtonOverrides } from './overrides'
import { PopOverProps } from './types'
import { Root } from './styled-components'

export const ThreeDotsPopover: VFC<PopOverProps> = props => (
  <Root onClick={e => e.stopPropagation()}>
    <StatefulPopover overrides={popoverOverrides} focusLock {...props}>
      <IconButton
        size={20}
        overrides={threeDotButtonOverrides}
        aria-label={props.ariaLabel}
      >
        <ThreeDots />
      </IconButton>
    </StatefulPopover>
  </Root>
)
