import { FC } from 'react'
import { FormattedMessage } from 'react-intl'
import { AllowedStatusKeys } from 'gql/types/globalTypes'
import { useSelection } from 'components/ag-grid'
import { useSurveyPreferences } from '../SurveysPreferencesProvider'
import { MakeActiveButton } from './MakeActiveButton'
import { MakeInactiveButton } from './MakeInactiveButton'
import { DeleteButton } from './DeleteButton'
import {
  Root,
  SelectionInfoContainer,
  ActionContainer,
  SelectionInfo,
} from './styled-components'
import { BulkActionsProps } from './types'

export const BulkActions: FC<BulkActionsProps> = props => {
  const { totalItems, appliedSearchTerm, ...restProps } = props
  const { getSelectedItems, isSelectAll, getExcludedItems } = useSelection()
  const { preferences } = useSurveyPreferences()
  const { createdBy, statuses } = preferences

  const bulkActionVariables = () => ({
    uuids: getSelectedItems(),
    isSelectAll: isSelectAll(),
    excludedUuids: getExcludedItems(),
    searchTerm: appliedSearchTerm,
    filters: {
      createdBy: createdBy?.map(({ uuid }) => uuid),
      status: statuses as AllowedStatusKeys[],
    },
  })

  return (
    <Root>
      <SelectionInfoContainer>
        <SelectionInfo>
          <FormattedMessage
            defaultMessage="{count, number} survey {count, plural, one {type} other {types}} selected"
            description="Section title"
            values={{
              count: isSelectAll()
                ? totalItems && totalItems - getExcludedItems().length
                : getSelectedItems().length,
            }}
          />
        </SelectionInfo>
      </SelectionInfoContainer>
      <ActionContainer>
        <MakeActiveButton variables={bulkActionVariables()} {...restProps} />
        <MakeInactiveButton variables={bulkActionVariables()} {...restProps} />
        <DeleteButton variables={bulkActionVariables()} {...restProps} />
      </ActionContainer>
    </Root>
  )
}
